body {
  margin: 0;
  padding: 20px 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2c3940;
  color: #d1d4d5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.tickers {
    background-color: #1b262d;
    width: calc(50% - 50px);
    margin: 0 20px;
    padding: 10px;
    display: inline-block;
    vertical-align: top;
}

.tickers-title {
    margin-top: 0;
    padding-bottom: 10px;
    margin-top: 0;
    text-align: left;
    border-bottom: 1px solid #31393d;
}

.ticker-header-wrapper {
    margin-bottom: 10px;
}

.ticker-header-row,
.ticker-data-row {
    display: inline-block;
    width: calc(25% - 10px);
    padding: 5px;
}

.ticker-header-row {
    border: 0;
}

.ticker-data-wrapper {
    cursor: pointer;
    border-bottom: 1px solid #29373b;
}

.ticker-data-wrapper:hover {
    background-color: #273138;
}

.lower {
    color: #aa5646;
}

.higher {
    color: #478d4a;
}

.ticker-currency {
    opacity: 0.6;
}
.order-book {
    margin: 20px;
    background-color: #1b262d;
    padding: 20px;
    padding-top: 0;
}

.order-book-list {
    width: 50%;
    display: inline-block;
}

.order-book-col {
    width: calc(33% - 10px);
    padding: 5px;
    display: inline-block;
}

.order-book-row {
    cursor: pointer;
}

.order-book-row:hover {
    background-color: #222c32;
}

.order-book-title {
    padding-top: 10px;
    padding-bottom: 10px;
    text-align: left;
    border-bottom: 1px solid #31393d;
}

.loading-order-book {
    text-align: center;
    display: inline-block;
    width: 50%;
}
.trades {
    background-color: #1b262d;
    padding: 10px;
    padding-top: 0;
    width: calc(50% - 50px);
    margin: 0;
    margin-right: 20px;
    display: inline-block;
}

.trades-title {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-top: 0;
    text-align: left;
    border-bottom: 1px solid #31393d;
}

.trades-col {
    display: inline-block;
    width: calc(33% - 10px);
    padding: 5px;
}

.minus-trade {
    background-color: rgba(225, 86, 86, 0.063);
}

.plus-trade {
    background-color: rgba(157, 194, 74, 0.05);
}
.app {
  text-align: center;
}

